/* reboot */
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1.3}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}

/* general styles */
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  background: #18191a;
  color: #fff;
}
a {
  color: #fff;
}

h1 {
  font-size: 2rem;
}

hr {
  border: 1px solid #3a3b3c;
  width: 100%;
  margin: 0 auto;
  margin-bottom: 1rem;
}

small {
  font-size: .7rem;
  font-style: italic;
  opacity: .7;
}

.text-left {
  text-align: left;
}

.main {
  margin: 0 auto;
  max-width: 700px;
  display: flex;
  flex-direction: column;
  padding: 2rem;
  gap: 1.8rem;
  text-align: center;
}

.logo {
  border-radius: 50%;
  margin: 0 auto
}

/* links list */
.links-list {
  width: 100%;
  margin: 1rem auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;

  li {
    width: 100%;
    border-radius: .8rem;
  }

  a {
    background: #242526;
    border: 1px solid #3a3b3c;
    display: flex;
    align-items: center;
    text-decoration: none;
    font-weight: 600;
    transition: all 0.2s ease-in-out;
    border-radius: 2rem;
    padding: .6rem;
    color: #fff;
    position: relative;
    overflow: hidden;
    max-width: 100%;

    &:hover {
      opacity: 0.7;
    }

    img {
      border-radius: 50%;
      margin-inline-end: 1rem;
    }

    @media only screen and (min-width: 768px) {
      span {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }
}

/* social list */
.social-list {
  margin: 0 auto;
  display: flex;
  gap: 1.5rem;

  a {
    color: #fff;
    text-decoration: none;
    font-weight: 600;
    transition: all 0.2s ease-in-out;

    &:hover {
      opacity: 0.7;
    }
  }
}
